$(document).ready(function() {
	$('#my-menu').mmenu();
});

var api = $('#my-menu').data('mmenu');
	api.bind('open:finish', function() {
		$('.burger').addClass('burger__line_active');
	});
	api.bind('close:finish', function() {
		$('.hamburger').removeClass('burger__line_active');
	});


